div.col.Product {
  /* height: 520px; */
  height: 40em;
}

div.row-cols-1,
div.row-cols-md-4 {
  margin-top: 2em;
}

div.card {
  width: 18rem;
  height: 90% !important;
}

div.Product {
  direction: ltr;
  padding-right: 3em;
}

div.col {
  margin-top: 0;
}

p.card-text {
  height: 6em;
  width: 18em;
  overflow: hidden;
  color: #d52e3f;
}
.row-cols-md-4 > * {
  max-width: 23%;
}

a.LinkCard ~ h5.card-title {
  font-weight: 600;
}

h5.card-title {
  direction: rtl;
  max-height: 24px;
}

.card-img-top {
  min-height: 14em;
  max-height: 14em;
}

.card-price {
  height: 2em;
  margin: 0em 0;
  font-size: 1em;
  text-align: initial;
}

