  transform: rotate(45deg);
  transition: all 0.5s;
}

/* Creates the shape of the circle by using input with a border-radius */
.containerS .search::after {
  content: "";
  position: absolute;
  margin: auto;
  top: -5px;
  right: 0;
  bottom: 0;
  left: -5px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: 2px solid white;
  transition: all 0.5s;
}

.containerS input {
  /* font-family: "Inconsolata", monospace; */
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 50px;
  border: none;
  background: #c74c64;
  color: white;
  text-shadow: 0 0 10px #c74c64;
  padding: 0 80px 0 20px;
  border-radius: 30px;
  box-shadow: 0 0 25px 0 #c74c64, 0 20px 25px 0 rgba(0, 0, 0, 0.2);
  transition: all 1s;
  opacity: 0;
  z-index: 5;
  /* font-weight: bolder; */
  letter-spacing: 0.1em;
}
.containerS input:hover {
  cursor: pointer;
}
.containerS input:focus {
  width: 300px;
  opacity: 1;
  cursor: text;
}
.containerS input:focus ~ .search {
  right: -250px;
  background: #ffffff;
  z-index: 6;
}

.containerS input:focus ~ .search::before {
  /* x color*/
  background-color: #c74c64;

  top: 0;
  left: 0;
  width: 25px;
}

.containerS input:focus ~ .search::after {
  top: 0;
  left: 0;
  width: 25px;
  height: 2px;
  border: none;
  background: white;
  border-radius: 0;
  /* x color*/
